.boton-contenedor{
    height: 60px;
    width: 40px;
    font-size: 1.6rem;
    display: flex;
    flex: 1;
    background-color: #504e4f;
  margin-top: 80px;
    margin-bottom: 10px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid rgb(0, 0, 0);
    cursor: pointer;
    user-select: none;
    font-family: Tahoma,Verdana,Segoe,sans-serif;
}
.boton-clear:hover{
    color: #fffb00;
    background-color: #615f60;
}

.boton-contenedor:hover{
    color: #fffb00;
    background-color: #615f60;
}

.operador{
    background-color: #00471b;
}
.operador:hover{
    background-color: #ff6600;
}